import {
    arctic
} from '../mainJs.js';

arctic.jsTricks = {
    remove: function () {
        $("[data-js-remove]").each(function(){
            $(this).remove();
        })
    },

    init: function () {
        arctic.jsTricks.remove();
    }
}

function tabs() {
    $('[data-tab]').each(function () { //        

        $(this).click(function (e) { //  ""  
			e.preventDefault();

            let buttonClass = $(this).attr('class').split(' ')[0]; //          

            let target = $(this).attr('data-tab'); //  "" 

            let targetClass = $('[data-index="' + target + '"]').attr('class').split(' ')[0]; //   ""

            $(this).closest('.tabs-btn').find("." + buttonClass).removeClass(buttonClass + "_active"); //         

            $('.tabs').find("." + targetClass).removeClass(targetClass + "_active"); //       "" 

            $(this).addClass(buttonClass + "_active"); //    

			$('[data-index="' + target + '"]').addClass(targetClass + "_active"); //  ""  
			
			
			//     . 
			setTimeout(function () {
				var mySwiper = $(".block-11_slider")[0].swiper;
				mySwiper.update();
			   }, 300);
		})
		
	})
	
}

$(function(){
	arctic.jsTricks.init();
	tabs();
	
})

